.hero-section{
    background: url('../public/background-home.jpg') no-repeat center;
    background-size: cover;
    width: 100%;
    height: 100vh;
    z-index: -1;
}

.hero-section::before{
    background-color: rgba(0, 0, 0, 0.6);
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: -1;
}

.login-form {
    min-height: 100vh;
    display: flex;
    justify-content: flex-end;
}

.signup-form{
    min-height: 100vh;
    display: flex;
    justify-content: flex-end;
}


/* dashboard */

.dashboard-section{
    margin-top: 80px;
    
}

.dashboard-wrapper{
    min-height: 100vh;
}